.table {
  margin: 0px 20px 25px;

  svg {
    color: #0097b2;

    &:hover {
      color: red;
    }
  }
}

.tableTitle {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 50px 20px 0px;
}

.date {
  font-size: 1.2rem;
  color: #ffffff;
}

.tableFooter {
  margin: 100px 20px 100px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
